"use client";

import { motion } from "framer-motion";
import { Barcode, User, ShieldAlert, Skull } from "lucide-react";

export interface TargetInfo {
    username: string;
    full_name: string;
    profile_pic: string;
}

export interface Threat {
    username: string;
    reason: string;
    profile_pic_url?: string;
}

interface ReceiptCardProps {
    score: number;
    verdictSummary?: string; // Mapped from 'verdict' in backend
    targetInfo?: TargetInfo;
    suspects?: Threat[];
}

export function ReceiptCard({ score, verdictSummary, targetInfo, suspects = [] }: ReceiptCardProps) {
    const isToxic = score < 50;

    return (
        <motion.div
            initial={{ scale: 0.9, opacity: 0 }}
            animate={{ scale: 1, opacity: 1 }}
            transition={{ type: "spring", stiffness: 100, damping: 20 }}
            className="relative w-full max-w-md mx-auto bg-[#050505] text-green-500 font-mono shadow-[0_0_50px_rgba(0,255,0,0.1)] overflow-hidden border border-green-900/50 rounded-sm"
            style={{
                backgroundImage: `
                    linear-gradient(rgba(0, 255, 0, 0.03) 1px, transparent 1px),
                    linear-gradient(90deg, rgba(0, 255, 0, 0.03) 1px, transparent 1px)
                `,
                backgroundSize: '20px 20px',
            }}
        >
            {/* Scanline Overlay */}
            <div className="absolute inset-0 pointer-events-none bg-[linear-gradient(transparent_50%,rgba(0,0,0,0.5)_50%)] bg-[length:100%_4px] opacity-20 z-20" />

            <div className="p-6 pb-12 relative z-10">
                {/* Header */}
                <div className="text-center border-b border-green-900/50 pb-6 mb-6">
                    <motion.div
                        animate={{ opacity: [1, 0.5, 1] }}
                        transition={{ duration: 2, repeat: Infinity }}
                        className="flex items-center justify-center gap-2 text-red-500 mb-4"
                    >
                        <ShieldAlert className="w-5 h-5" />
                        <h1 className="text-xl font-bold tracking-widest uppercase">SYSTEM BREACH DETECTED</h1>
                        <ShieldAlert className="w-5 h-5" />
                    </motion.div>

                    {targetInfo && (
                        <div className="flex flex-col items-center relative">
                            <div className="relative mb-6 group">
                                <div className="absolute inset-0 bg-red-500 blur-xl opacity-20 group-hover:opacity-40 transition-opacity duration-500 rounded-full" />
                                <div className="w-32 h-32 rounded-full border-2 border-red-500 overflow-hidden relative shadow-[0_0_20px_rgba(220,38,38,0.5)]">
                                    {targetInfo.profile_pic ? (
                                        <img
                                            src={targetInfo.profile_pic}
                                            alt="Target"
                                            className="w-full h-full object-cover grayscale contrast-125 hover:grayscale-0 transition-all duration-500"
                                            onError={(e) => {
                                                (e.target as HTMLImageElement).style.display = 'none';
                                                (e.target as HTMLImageElement).nextElementSibling?.classList.remove('hidden');
                                            }}
                                        />
                                    ) : null}
                                    <div className={`w-full h-full bg-black flex items-center justify-center ${targetInfo.profile_pic ? 'hidden' : ''}`}>
                                        <User className="w-16 h-16 text-red-900" />
                                    </div>

                                    {/* Target Acquired Overlay */}
                                    <div className="absolute inset-0 flex items-center justify-center pointer-events-none">
                                        <div className="w-[90%] h-[90%] border border-red-500/30 rounded-full animate-ping absolute" />
                                    </div>
                                </div>
                                <div className="absolute -bottom-3 left-1/2 transform -translate-x-1/2 bg-red-600 text-black text-[10px] font-black px-3 py-0.5 uppercase tracking-widest shadow-lg">
                                    TARGET ACQUIRED
                                </div>
                            </div>
                            <h2 className="text-lg font-bold uppercase text-white tracking-wider">{targetInfo.full_name}</h2>
                            <p className="text-xs text-green-600 font-bold tracking-widest">@{targetInfo.username}</p>
                        </div>
                    )}
                </div>

                {/* Score Section */}
                <div className="text-center mb-8 relative">
                    <p className="text-[10px] uppercase font-bold text-green-700 mb-2 tracking-[0.2em]">LOYALTY INTEGRITY SCORE</p>
                    <div className={`text-7xl font-black tracking-tighter leading-none ${isToxic ? 'text-red-500' : 'text-green-500'} drop-shadow-[0_0_10px_rgba(0,0,0,0.8)]`}>
                        <span className="animate-pulse">{score}</span>
                        <span className="text-2xl text-green-900 ml-1">%</span>
                    </div>
                </div>

                {/* Verdict Box */}
                {verdictSummary && (
                    <div className="mb-8 p-4 bg-red-950/20 border border-red-900/50 rounded relative overflow-hidden">
                        <div className="absolute top-0 left-0 w-1 h-full bg-red-600" />
                        <div className="flex items-start gap-3">
                            <Skull className="w-5 h-5 text-red-500 shrink-0 mt-0.5" />
                            <div>
                                <p className="text-[10px] uppercase font-bold text-red-500 mb-1 tracking-widest">AI FORENSIC VERDICT</p>
                                <p className="text-sm font-bold leading-relaxed text-red-100/90 font-mono">
                                    "{verdictSummary}"
                                </p>
                            </div>
                        </div>
                    </div>
                )}

                {/* Hit List */}
                <div className="border-t border-green-900/50 pt-4">
                    <div className="flex justify-between items-center mb-4 text-[10px] font-bold uppercase text-green-700 tracking-widest">
                        <span>DETECTED THREATS</span>
                        <span>STATUS: ACTIVE</span>
                    </div>

                    <div className="space-y-3 font-mono text-xs max-h-80 overflow-y-auto pr-2 scrollbar-thin scrollbar-thumb-green-900 scrollbar-track-black">
                        {suspects.length > 0 ? (
                            suspects.map((suspect, index) => (
                                <div key={index} className="flex flex-col gap-1 group">
                                    <div className="flex items-center gap-2 text-green-400">
                                        <span className="text-green-800">{">"}</span>
                                        <span className="font-bold group-hover:text-green-300 transition-colors">@{suspect.username}</span>
                                    </div>
                                    <div className="pl-5 text-[10px] text-green-600/80 uppercase tracking-tight">
                                        [REASON: <span className="text-red-400">{suspect.reason}</span>]
                                    </div>
                                </div>
                            ))
                        ) : (
                            <p className="text-center text-xs text-green-800 py-4">NO ANOMALIES DETECTED IN NETWORK.</p>
                        )}
                    </div>
                </div>

                {/* Footer */}
                <div className="mt-8 pt-6 border-t border-green-900/50 text-center space-y-2 opacity-50">
                    <Barcode className="w-full h-8 text-green-800" />
                    <p className="text-[8px] uppercase tracking-[0.3em] text-green-800">ENCRYPTED // CAUGHTIN4K.AI // DO NOT DISTRIBUTE</p>
                </div>
            </div>
        </motion.div>
    );
}
